.text1 {
  color: blue;
  -webkit-animation-name: texto1;
          animation-name: texto1;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}
.text2 {
  color: red;
  -webkit-animation-name: texto1;
          animation-name: texto1;

  -webkit-animation-duration: 5s;

          animation-duration: 5s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

@-webkit-keyframes texto1 {
  from {
    color: rgb(255, 56, 56);
    text-shadow: 1px 1px rgb(1, 255, 196);
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    color: rgb(0, 51, 255);
    text-shadow: 1px 1px rgb(252, 252, 252);
  }
}

@keyframes texto1 {
  from {
    color: rgb(255, 56, 56);
    text-shadow: 1px 1px rgb(1, 255, 196);
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    color: rgb(0, 51, 255);
    text-shadow: 1px 1px rgb(252, 252, 252);
  }
}

#Carousel-container img {
  border-radius: 20px;
  border: 5px solid whitesmoke;
}

#slider {
  max-width: 95%;
  overflow: hidden;
  margin-top: 5px;

  -webkit-align-items: center;

          align-items: center;
  margin-left: 2.9%;
}

#slider figure {
  position: relative;
  width: 500%;
  top: 1px;
  margin: 0px;
  left: 0;
  -webkit-animation: 20s slider infinite;
          animation: 20s slider infinite;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

#slider figure img {
  width: 20%;
  float: left;
}

@-webkit-keyframes slider {
  0% {
    left: 0%;
  }
  20% {
    left: 0%;
  }
  25% {
    left: -100%;
  }
  45% {
    left: -100%;
  }
  50% {
    left: -200%;
  }
  70% {
    left: -200%;
  }
  75% {
    left: -300%;
  }
  95% {
    left: -300%;
  }
  100% {
    left: -400%;
  }
  100% {
    left: -400%;
  }
}

@keyframes slider {
  0% {
    left: 0%;
  }
  20% {
    left: 0%;
  }
  25% {
    left: -100%;
  }
  45% {
    left: -100%;
  }
  50% {
    left: -200%;
  }
  70% {
    left: -200%;
  }
  75% {
    left: -300%;
  }
  95% {
    left: -300%;
  }
  100% {
    left: -400%;
  }
  100% {
    left: -400%;
  }
}

